body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}
ol, ul {
  padding-left: 30px;
}

.status {
  margin-bottom: 10px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}
.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}
.square.O {
  color:red;
}
.square.highlite-square {
  color:blue;
}
.square:focus {
  outline: none;
}
.board-row:nth-of-type(even) .square:nth-child(odd) {background: #f6f6f6}
.board-row:nth-of-type(odd) .square:nth-child(even) {background: #f6f6f6}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.game-info {
  margin-left: 20px;
}

.game-button {
  margin: 0 0;
}
.mode-label {
  margin: 40px 0 10px 0;
}
.lang-selector, .readme-link {
  margin: 40px 0 0 0;
}

.board.react-resizable {
  position: relative;
}
.react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  bottom: 0;
  right: 0;
  background: url('data:image/svg+xml;base64,');
  background-position: bottom right;
  padding: 0 3px 3px 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
}
